<template>
  <div class="singer">
    <top :pic2="p1"></top>
    <list :options="p"></list>
  </div>
</template>

<script>
import list from "./components/list";
import top from "./components/top";
export default {
  data() {
    return {
      p: [],
      p1: []
    };
  },
  components: {
    list,
    top
  },
  created() {
    this.getsinger();
  },
  methods: {
    getsinger() {
        // 、将所有歌手的歌曲都显示在list列表；
      this.axios.get("/getmusiclist").then(res => {
        let result = res.data;
        for (let i in result) {
          this.p.push(result[i]);
        }
        // 将所有数据进行过滤，将lid相同的都去掉；留下lid不同的；
        let a = result.filter(function(elem, i, arr) {
          return i < arr.length - 1 ? elem.lid != arr[i + 1].lid : true;
        });
        // 将过滤后的歌手赋给top；
        for (let b in a) {
          this.p1.push(a[b]);
        }
      });
    }
  }
};
</script>

